import React from "react"
import Fade from "react-reveal"

export default function About({ data }) {
    if (!data) return null

    const {
        address: { city, state, zip },
        bio,
        email,
        image,
        name,
        phone,
        resumePath,
    } = data
    const profilepic = `images/${image}`

    return (
        <section id="about">
            <Fade duration={1000}>
                <div className="row">
                    <div className="three columns">
                        <img
                            className="profile-pic"
                            src={profilepic}
                            alt="Nordic Giant Profile Pic"
                        />
                    </div>
                    <div className="nine columns main-col">
                        <h2>About Me</h2>
                        <p>{bio}</p>
                        <div className="row">
                            <div className="columns contact-details">
                                <h2>Contact Details</h2>
                                <p className="address">
                                    <span>{name}</span>
                                    <br />
                                    <span>
                                        {city} {state}, {zip}
                                    </span>
                                    <br />
                                    <span>{phone}</span>
                                    <br />
                                    <span>{email}</span>
                                </p>
                            </div>
                            <div className="columns download">
                                <p>
                                    <a
                                        href={resumePath}
                                        rel="noreferrer"
                                        target="_blank"
                                        className="button"
                                    >
                                        <i className="fa fa-file"></i>
                                        Resume
                                    </a>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </Fade>
        </section>
    )
}
